<template>
  <div>
    <div class="h-16 border-b flex items-center px-4 justify-between">
      <div class="flex items-center">
        <router-link to="/">
          <img class="w-6 h-6" src="../assets/logo.png" />
        </router-link>
        <span class="px-4">|</span>
        {{config.name}}
      </div>
      <div class="flex items-center">
        <a class="ml-5" :href="config.source" target="_brack">文档</a>
        <a class="ml-5" :href="editUrl + 'index.json'" target="_brack">配置</a>
      </div>
    </div>
    <waterfall :col="col" :gutterWidth="0" :data="items" class="p-1">
      <template>
        <div class="p-2 item" v-for="(item,index) in items" :key="index">
          <div class="border rounded">
            <div class="h-12 flex items-center justify-between px-3 md-bg-color">
              <div>{{item.title}}</div>
              <a :href="item.edit" target="_brack">
                <img class="w-5 h-5 github-icon" src="https://github.com/fluidicon.png" />
              </a>
            </div>
            <div class="markdown-body" v-html="item.content"></div>
          </div>
        </div>
      </template>
    </waterfall>
  </div>
</template>

<script>
import axios from "axios";
import marked from "marked";
import hljs from "highlight.js";
export default {
  name: "App",
  data() {
    return {
      rawUrl: "https://raw.githubusercontent.com/Ace520/sheet/gh-pages/md/",
      editUrl: "https://github.com/Ace520/sheet/edit/gh-pages/md/",
      config: {},
      items: [],
      col: 3
    };
  },
  mounted() {
    marked.setOptions({
      highlight: function(code) {
        return hljs.highlightAuto(code).value;
      }
    });
    let col = parseInt(document.body.clientWidth / 450);
    this.switchCol(col > 1 ? col : 1);
    this.config = require("../../md/" + this.$route.params.id + "/index.json");
    this.rawUrl = this.rawUrl + this.config.path + "/";
    this.editUrl = this.editUrl + this.config.path + "/";
    this.initItems();
    this.appendLanguageJs();
  },
  methods: {
    scroll(scrollData) {
      console.log(scrollData);
    },
    switchCol(col) {
      this.col = col;
    },
    initItems() {
      let items = this.config.items;
      for (let i = 0; i < items.length; i++) {
        axios
          .get(this.rawUrl + items[i].path)
          .then(res => {
            let content = marked(res.data);
            this.items.push({
              title: items[i].title,
              content: content,
              edit: this.editUrl + items[i].path
            });
          })
          .catch(error => {
            console.error(error);
          });
      }
    },
    appendLanguageJs() {
      if (!this.config.languages) {
        return;
      }
      let languages = this.config.languages.split(",");
      for (let i = 0; i < languages.length; i++) {
        const s = document.createElement("script");
        s.type = "text/javascript";
        s.src =
          "https://cdn.bootcss.com/highlight.js/8.7/languages/" +
          languages[i] +
          ".min.js";
        document.body.appendChild(s);
      }
    }
  }
};
</script>

<style>
.github-icon {
  display: none;
}
.item:hover .github-icon {
  display: block;
}
.md-bg-color {
  background-color: #f6f8fa;
}
</style>
